@font-face {
  font-family: "Glacial Indifference";
  src: url("./assets/fonts/GlacialIndifference-Regular.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Glacial Indifference";
  src: url("./assets/fonts/GlacialIndifference-Bold.otf") format("opentype");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("./assets/fonts/CormorantGaramond-Italic-VariableFont_wght.ttf") format("truetype");
  font-weight: 300 700; /* same range */
  font-style: italic;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("./assets/fonts/CormorantGaramond-VariableFont_wght.ttf") format("truetype");
  font-weight: 300 700; /* range of supported weights */
  font-style: normal;
}

body {
  font-family: "Cormorant Garamond", serif;
  color: #111;
  font-weight: 400;
  background: #fff;
  text-align: center;
  margin: 0;
}

.hero {
  padding: 3rem 1rem;
  background: #99964c;
  color: white
}

.logo {
  font-family: "Glacial Indifference", sans-serif;
  font-weight: 700;
  font-size: 3rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 0;
}

nav {
  margin: 1rem 0;
}

nav a {
  margin: 0 15px;
  text-decoration: none;
  font-weight: bold;
  color: black;
}

h2 {
  margin-top: 2rem;
  font-size: 2rem;
  font-family: "Playfair Display", serif;
}

.pass {
  width: 100vw;
  height: 100vw;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  background: #99964c;
}

.pass h2 {
  color: white;
  font-family: "Glacial Indifference", sans-serif;
}

.pass input {
  width: 100%;
  max-width: 280px;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  font-family: "Cormorant Garamond", serif;
  border: 1px solid #99964c;
  background-color: white;
  transition: all 0.3s ease;
}

.pass input:focus {
  border-color: #000;
  box-shadow: 0 0 0 3px rgba(0,0,0,0.1);
}

.pass input::placeholder {
  color: #999;
  font-style: italic;
}

.pass button {
  margin-top: 0.5rem;
  padding: 0.75rem 1.5rem;
  font-family: "Glacial Indifference", sans-serif;
  font-size: 0.95rem;
  letter-spacing: 1px;
  text-transform: uppercase;

  border: none;
  border-radius: 8px;
  background: #544e2e;
  color: white;

  cursor: pointer;
}

.pass button:hover {
  background: #4a4529;
}
